import { defineStore } from 'pinia'
import { getAuth, signInWithEmailAndPassword, sendPasswordResetEmail, createUserWithEmailAndPassword, updateProfile, signOut } from "firebase/auth";
import Toast from '@/main'
const user = JSON.parse(localStorage.getItem('Userdata'))
getAuth().languageCode = 'en';

export const useAuthStore = defineStore('Auth', {
    state: () => {
        return {
            user: user,
            error: null,
            isAuth: false
        }
    },
    getters: {
        isAuthenticated: (state) => !!state.user
    },
    actions: {
        async signIn(email, password) {
            try {
                const { user } = await signInWithEmailAndPassword(getAuth(), email, password)
                localStorage.setItem("Userdata", JSON.stringify(user))
                localStorage.setItem("isAuth", true)
                this.user = user
                this.isAuth = true
                Toast.fire({
                    icon: "success",
                    title: this.user.displayName + ", Signed in successfully",
                });
            } catch (error) {
                this.error = error.code
                Toast.fire({
                    icon: "error",
                    title: this.error,
                });
            }
        },
        async resetPassword(email) {
            try {
                await sendPasswordResetEmail(getAuth(), email)
                Toast.fire({
                    icon: "info",
                    title: "please check your email.",
                });
            } catch (error) {
                this.error = error.code;
                Toast.fire({
                    icon: "error",
                    title: this.error,
                });
            }
        },
        async logout() {
            try {
                this.user = null
                this.isAuth = false
                this.error = null,
                this.isAuthenticated = false
                localStorage.clear();
                await signOut(getAuth())
            } catch (error) {
                this.error = error.message
            }
        },
        async Registration(username, email, password) {
            try {
                await createUserWithEmailAndPassword(getAuth(), email, password)
                    .then((userCredential) => {
                        // Signed in 
                        const CredentialUser = userCredential.user;
                        updateProfile(getAuth().currentUser, { displayName: username })
                            .then(() => {
                                localStorage.setItem("Userdata", JSON.stringify(CredentialUser))
                                localStorage.setItem("isAuth", true)
                                this.user = CredentialUser.providerData[0]
                                this.isAuth = true
                                Toast.fire({
                                    icon: "success",
                                    title: username + "Signed Up successfully",
                                });
                            });
                    })
            } catch (error) {
                this.error = error.code;
                Toast.fire({
                    icon: "error",
                    title: this.error,
                });
            }
        },
        // LoginWithGoogle() {

        // }
    }
})